"use client";

import {
  Line,
  LineChart,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

interface TimeSeriesChartProps {
  data: { label: string; value: number }[];
  yLabel?: string;
  suffix?: string;
}

export function TimeSeriesChart({
  data,
  yLabel,
  suffix,
}: TimeSeriesChartProps) {
  return (
    <div className="h-56 w-full">
      <ResponsiveContainer width="100%" height="100%">
        <LineChart data={data} margin={{ left: 8, right: 8, top: 8, bottom: 8 }}>
          <CartesianGrid strokeDasharray="3 3" vertical={false} />
          <XAxis
            dataKey="label"
            tickLine={false}
            axisLine={false}
            tickMargin={8}
          />
          <YAxis
            tickLine={false}
            axisLine={false}
            width={40}
            tickMargin={4}
            tickFormatter={(v) =>
              suffix === "%"
                ? `${v}`
                : v >= 1_000_000
                  ? `${(v / 1_000_000).toFixed(1)}M`
                  : v >= 1_000
                    ? `${(v / 1_000).toFixed(0)}k`
                    : `${v}`
            }
          />
          <Tooltip
            formatter={(value: number) =>
              suffix ? `${value.toFixed(1)}${suffix}` : value.toLocaleString("pt-BR")
            }
            labelFormatter={(label) => `${label}`}
          />
          <Line
            type="monotone"
            dataKey="value"
            stroke="hsl(var(--primary))"
            strokeWidth={2}
            dot={{ r: 3 }}
            activeDot={{ r: 4 }}
          />
        </LineChart>
      </ResponsiveContainer>
      {yLabel ? (
        <p className="mt-1 text-xs text-muted-foreground">{yLabel}</p>
      ) : null}
    </div>
  );
}


